<template>
  <div id="app">
    <transition :name="transitionName">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {
      transitionName: "slide-right",
    };
  },
  watch: {
  '$route' (to) {
      const toDepth = to.path;
      this.transitionName = toDepth === '/suggest' ? 'slide-left' : 'slide-right';
    }
  },
};
</script>

